//universal reset
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  max-width: 100%;
}

html {
  font-size: 62.5%;
}
h1 {
  font-size: 1.8rem;
}
.App {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  width: 1200px;
  background-color: silver;
  
}
.Buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  width: 800px;
  padding: 2% 0% 

  
}
.InfoBar {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  max-width: 100%;
  width: 100%;

  
}
.Title{
  font-size: 3.2rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
